.header {
  display: block;
  max-width: 100%;
  min-height: 70px;
  background: #FFFFFF;
  box-shadow: 0 1px 4px rgba(28, 27, 26, 0.07);
}

.header__nav-container {
  display: block;
  max-width: 1200px;
  margin: 0 auto;
}

.header__logo {
  display: block;
  float: left;
  margin: 24px 0;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(#0B3997 2.76%, #2B8AFF 98.34%);
  mix-blend-mode: normal;
}

.header__menu {
  display: block;
  float: left;
  margin: 25px 0 25px 316px;
}

.header__menu-item {
  display: inline-block;
  color: #1C1B1A;
  padding-left: 0;
  margin-right: 20px;
}

.header__menu-item:first-child a {
  color: #1C1B1A;
  border-bottom: 3px solid #2985F7;
  opacity: 1;
}

.header__menu-item a {
  text-align: center;
  text-decoration: none;
  border-bottom: 3px solid transparent;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #1C1B1A;
  opacity: 0.5;
  padding: 25px 10px 23px;
}

.header__menu-item a:hover {
  opacity: 1;
  border-color: rgba(41, 133, 247, 0.4);
  transition: 0.8s;
}

.header__burger {
  display: none;
  position: absolute;
  right: 60px;
  top: 24px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: transparent;
  font-size: 0;
  background: url("../assets/menu-button.png");
  z-index: 1;
}

.header__checkbox {
  display: none;
}

.header__checkbox:checked + .header__burger {
  background-image: url("../assets/close.svg");
}

.header__checkbox:checked ~ .header__menu {
  display: block;
}

@media all and (max-width: 1365px) {
  .header {
    padding: 0 80px;
  }
}

@media all and (max-width: 1050px) {
  .header__menu {
    float: right;
    margin: 25px 0;
  }
}

@media all and (max-width: 768px) {
  .header__burger {
    display: block;
  }

  .header__menu {
    min-height: 156px;
    clear: left;
    float: left;
    display: none;
    width: 100%;
    text-align: center;
    padding-left: 0;
    margin-bottom: 0;
  }

  .header__menu-item {
    display: block;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .header__menu-item:first-child a {
    color: #1C1B1A;
    border-bottom: 3px solid transparent;
    opacity: 0.5;
  }

  .header__menu-item:first-child a:hover {
    opacity: 1;
    border-color: #2985F7;
    transition: 0.8s;
  }

  .header__menu-item a {
    padding: 10px;
  }
}

@media all and (max-width: 375px) {
  .header {
    padding: 0 30px;
  }

  .header__burger {
    right: 30px;
  }
}

