.copyright {
  display: block;
  width: 100%;
  border-top: 1px solid #E8E8E8;
  margin-top: 40px;
}

.copyright__container {
  max-width: 1200px;
  margin: 0 auto;
}

.copyright__trademark {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 16px;
  font-size: 12px;
  color: #4D4D4D;
  float: left;
  margin: 30px 0 33px;
}

.copyright__menu {
  display: block;
  float: left;
  margin-top: 28px;
}

.copyright__menu-item {
  display: inline-block;
  margin-left: 30px;
}

.copyright__menu-item a {
  text-decoration: none;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.7);
  padding-bottom: 8px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
}

.copyright__menu-item a:hover {
  color: #32bfd5;
}

.copyright__menu-item a:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: #2498ee;
  height: 3px;
  transition-property: right;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.copyright__menu-item a:hover:before,
.copyright__menu-item a:focus:before,
.copyright__menu-item a:active:before {
  right: 0;
}

@media all and (max-width: 1365px) {
  .copyright__container {
    margin-left: 60px;
  }
}

@media all and (max-width: 768px) {
  .copyright__menu {
    display: none;
  }
}

@media all and (max-width: 760px) {
  .copyright__container {
    margin-left: 0;
  }

  .copyright__trademark {
    width: 100%;
    text-align: center;
  }
}

